.shelf {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: flex-end;
          align-items: flex-end;
  padding: 0;
  overflow: hidden;
  margin-top: 40px;
}

.shelf::after {
  content: "";
  position: absolute;
  width: 60vmin;
  bottom: 0;
  border-bottom: 6px solid rgba(159, 58, 58, .8);
}

.book {
  position: relative;
  list-style-type: none;
  height: 60px;
  width: 25px;
  border: 3px solid rgba(159, 58, 58, 1);
  margin: 0 1px 4px;
  padding: 0;
  border-radius: 2px;
  -webkit-transform-origin: bottom left;
          transform-origin: bottom left;
  /* animation: move-across 4500ms linear infinite; */
}

.book::before {
  content: "";
  position: absolute;
  top: 10%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 100%;
  border: 2px solid rgba(159, 58, 58, 1);
}

.book::after {
  content: "";
  position: absolute;
  bottom: 10%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 100%;
  /* border: 5px solid rgba(159, 58, 58, 1); */
}

.one {
  width: 20px;
  -webkit-transform: rotate(3deg);
          transform: rotate(3deg);
  margin-right: 1px;
}

.one::after {
  content: "";
  position: absolute;
  bottom: 10%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 100%;
  border: 2px solid rgba(159, 58, 58, 1);

}

.two {
  height: 70px;
}
.two::before {
  border: none;
}
.two::after {
  border: 6px solid rgba(159, 58, 58, 1);
}

.three {
  height: 72px;
  width: 22px;
  -webkit-transform: rotate(-6deg);
          transform: rotate(-6deg);
  margin-left: 5px;
}
.three::before {
  top: 20%
}
.three::after {
  bottom: 65%;
  border: 2px solid rgba(159, 58, 58, 1);
}

.four {
  height: 78px;
}
.four::after {
  border: 2px solid rgba(159, 58, 58, 1);
}


.five {
  width: 20px;
  margin: 0 0 4px;
  overflow: hidden;
}
.five::before {
  top:40%;
  padding: 10px;
  border: 6px solid rgba(159, 58, 58, 1);
}


.six {
  height: 72px;
  width: 25px;
}
